<template>
  <div class="container px-1">
    <p class="title is-3">Salesforce action generator</p>
    <div class="columns">
      <div class="column is-6">
        <p class="title is-4">Action configuration</p>
        <b-field label="Action type*:">
          <b-select
            v-model="action.renderActiontype"
            class="form-control"
            expanded
          >
            <option value="WEBVIEW_ACTION" selected>WEBVIEW_ACTION</option>
            <option value="FEATURE_ACTION">FEATURE_ACTION</option>
            <option value="BROWSER_ACTION">BROWSER_ACTION</option>
          </b-select>
        </b-field>
        <b-field label="Webview title*:">
          <b-input
            v-model="action.title"
            type="text"
            class="form-control"
            placeholder="e.g. Contratar seguro"
            expanded
          />
        </b-field>
        <b-field label="Webview top-title*:">
          <b-input 
          v-model="action.topTitle" 
          placeholder="e.g. Contratar seguro"
          type="text" 
          class="form-control" />
        </b-field>
        <b-field label="Destination URL (openUrl)*:">
          <b-input
            v-model="action.openURL"
            type="text"
            class="form-control"
            placeholder="e.g. https://example.com"
          />
        </b-field>
        <b-field label="Close URL*:">
          <b-input
            v-model="action.closeURL"
            type="text"
            class="form-control"
            placeholder="e.g. https://example.com"
          />
        </b-field>
        <!-- Adicionamos componente parámetros con nombre de parameters_prop y con listener para cambiar el estado de isParameters -->
        <CompParameters
          :parameters_prop="parameters"
          @isParameters="onIsParametersChange"
        ></CompParameters>
        <hr />
        <div class="buttons">
          <b-button type="is-primary" @click="submit" expanded
            >Get JSON offer</b-button
          >
        </div>
      </div>
      <div class="column is-6" v-if="finalJson">
        <div class="buttons is-centered">
          <b-button
            @click="copyText"
            icon-left="content-copy"
            class="is-secondary"
          >
            Copy to clipboard
          </b-button>
          <b-button
            @click="formatJson"
            icon-left="auto-fix"
            class="is-secondary"
          >
            {{ buttonText }}
          </b-button>
        </div>
        <pre v-bind:class="!isJsonBeautify ? 'minifyJson': null" id="canva">{{ finalJson }}</pre>
      </div>
    </div>
  </div>
</template>
  
  <script>
import CompParameters from "../components/C_Parameters.vue";
export default {
  name: "App",
  components: {
    CompParameters,
  },
  data: () => ({
    action: {
      renderActiontype: "WEBVIEW_ACTION",
      data: "",
      operationalAction: "usecase",
      title: "",
      topTitle: "",
      openURL: "",
      closeURL: "",
      parameters: {},
    },
    finalJson: undefined,
    isJsonBeautify: true,
    buttonText: "Minify json",
    isParameters: false,
    parameters: [
      {
        key: "token",
        value: "$TOKEN",
      },
    ],
    defaultParameters: {
      token: "$TOKEN",
    },
  }),

  methods: {
    submit() {
      if (this.isParameters) {
        this.action.parameters = this.setParameters();
      } else {
        this.action.parameters = this.defaultParameters;
      }
      this.action.data = this.action.renderActiontype;
      this.beautifyJson();
      this.scroll("canva");
      this.snackbar("The JSON has been generated successfully!");
      this.$GA.sendGenerateAction(this.action, this.$route)
    },
    copyText() {
      navigator.clipboard.writeText(this.finalJson);
      this.snackbar("JSON copied to clipboard!");
    },
    snackbar(text) {
      this.$buefy.snackbar.open(text);
    },
    async scroll(elementId) {
      await this.$nextTick();
      document.getElementById(elementId).scrollIntoView({
        behavior: "auto",
        block: "center",
        inline: "center",
      });
    },
    setParameters() {
      var newParameters = {};
      this.parameters.forEach((item) => {
        if ((item.key !== "" || undefined) && (item.value !== "" || undefined))
          newParameters[item.key] = item.value;
      });
      return newParameters;
    },
    beautifyJson() {
      this.isJsonBeautify = true;
      this.buttonText = "Minify json";
      return (this.finalJson = JSON.stringify(this.action, null, 2));
    },
    minifyJson() {
      this.isJsonBeautify = false;
      this.buttonText = "Beautify json";
      return (this.finalJson = JSON.stringify(this.action, null, 0));
    },
    formatJson() {
      this.isJsonBeautify ? this.minifyJson() : this.beautifyJson();
    },
    onIsParametersChange(value) {
      this.isParameters = value;
    },
  },
};
</script>
  
<style>
html,
body {
  height: 100%; /*both html and body*/
}
.minifyJson{
    word-wrap: break-word;
    white-space: normal
}
</style>
  