import Vue from 'vue'
import App from './App.vue'
import Buefy from 'buefy'
import router from './router'
import VueCookies from 'vue-cookies'

Vue.use(VueCookies, { expires: '1y'})
Vue.use(Buefy, {
  defaultIconPack: 'mdi'
})

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')