<template>
  <section>
    <b-field label="Parameters:">
      <b-switch
        v-model="isParameters"
        @input="emitParametersToParent"
        type="is-success"
      >
        Add/edit POST parameters
      </b-switch>
    </b-field>
    <div class="column is-11 is-offset-1" v-if="isParameters">
      <b-field
        :label="'Parameter ' + (index + 1)"
        v-for="(item, index) in parameters"
        :key="index"
      >
        <b-input
          v-model="item.key"
          type="text"
          class="form-control"
          placeholder="Key"
        />
        <b-input
          v-model="item.value"
          type="text"
          class="form-control"
          placeholder="Value"
        />
        <b-button
          type="is-danger  is-light"
          icon-right="delete"
          @click="deleteItem(item, index)"
        >
        </b-button>
      </b-field>
      <b-button
        type="is-ghost"
        size="is-small"
        @click="addParameter"
        outlined
        icon-left="plus"
      >
        Add new parameter</b-button
      >
    </div>
  </section>
</template>
<script>
export default {
  name: "App",
  props: {
    parameters_prop: Array,
    isParameters_prop: Boolean,
  },
  data: function () {
    return {
      parameters: this.parameters_prop, //mutate the prop, dejo documentacio para pepe del futro https://v2.vuejs.org/v2/guide/components-props.html?redirect=true#One-Way-Data-Flow
      isParameters: this.isParameters_prop,
    };
  },
  methods: {
    deleteItem(item, index) {
      if (this.parameters[index] === item) {
        this.parameters.splice(index, 1);
      } else {
        let found = this.parameters.indexOf(item);
        this.parameters.splice(found, 1);
      }
      if (this.parameters.length === 0) {
        //si nos quedamos sin ofertas añadimos una en blanco
        this.addParameter();
      }
      this.snackbar(
        "The parameter " + (index + 1) + " has been removed successfully!"
      );
    },
    addParameter() {
      this.parameters.push({ key: "", value: "" });
    },
    snackbar(text) {
      this.$buefy.snackbar.open(text);
    },
    emitParametersToParent() {
      this.$emit("isParameters", this.isParameters);
    },
  },
};
</script>