<template>
  <div class="container px-1">
    <p class="title is-3">Image & Icon Banners</p>
    <div class="columns">
      <div class="column is-6">
        <GenericVariables
          :isCarousel_prop="false"
          :offer_prop="offer"
        ></GenericVariables>

        <!-- IMAGE BANNER -->
        <b-collapse
        class="column is-11 is-offset-1"
        animation="slide"
        v-if="offer.rendering === 'IMAGE_BANNER'"
        >
          <template #trigger="props">
            <p class="title is-5 is-icon">
              Image banner variables
              <a>
                <b-icon :icon="props.open ? 'chevron-up' : 'chevron-down'" size='is-large' type="is-dark">
                </b-icon>
              </a>
            </p>
          </template>
          <b-field label="Image/Picture URL*:">
            <b-input
              v-model="renderingImageBanner.url"
              type="text"
              class="form-control"
              placeholder="https://example.com"
            />
          </b-field>
        </b-collapse>

        <!-- ICON BANNER -->
        <b-collapse
        class="column is-11 is-offset-1"
        animation="slide"
        v-if="offer.rendering === 'ICON_BANNER'"
        >
          <template #trigger="props">
            <p class="title is-5 is-icon">
              Icon banner variables
              <a>
                <b-icon :icon="props.open ? 'chevron-up' : 'chevron-down'"  size='is-large' type="is-dark">
                </b-icon>
              </a>
            </p>
          </template>
          <b-field label="Icon URL *:">
            <b-input
              v-model="renderingIconBanner.icon"
              type="text"
              class="form-control"
              placeholder="https://example.com"
            />
          </b-field>
          <b-field label="Background color (hex format):">
            <b-input
              v-model="renderingIconBanner.backgroundColor"
              type="text"
              class="form-control"
              placeholder="#000000"
              expanded
            />
            <b-colorpicker
              :value="renderingIconBanner.backgroundColor"
              @input="
                renderingIconBanner.backgroundColor = $event.toString('hex')
              "
            />
          </b-field>
          <b-field label="Banner text*:">
            <b-input
              v-model="renderingIconBanner.title"
              type="text"
              class="form-control"
              placeholder="Texto de pruebas"
            />
          </b-field>
          <b-field label="Text color (hex format)*:">
            <b-input
              v-model="renderingIconBanner.titleTextColor"
              type="text"
              class="form-control"
              placeholder="#FFFFFF"
              expanded
            />
            <b-colorpicker
              :value="renderingIconBanner.titleTextColor"
              @input="
                renderingIconBanner.titleTextColor = $event.toString('hex')
              "
            />
          </b-field>
        </b-collapse>

        <!-- ACTION -->
        
        <b-collapse
        class="column is-11 is-offset-1"
        animation="slide"
        v-if="
            offer.rendering == 'IMAGE_BANNER' ||
            offer.rendering == 'ICON_BANNER' ||
            offer.rendering == 'BANNER'
            "
        >
        <template #trigger="props">
            <p class="title is-5 is-icon">
              Action configuration
              <a>
                <b-icon :icon="props.open ? 'chevron-up' : 'chevron-down'"  size='is-large' type="is-dark">
                </b-icon>
              </a>
            </p>
          </template>
          <b-field label="Action type*:">
            <b-select v-model="actions.type" class="form-control" expanded>
              <option value="WEBVIEW_ACTION" selected>WEBVIEW_ACTION</option>
              <option value="FEATURE_ACTION">FEATURE_ACTION</option>
              <option value="BROWSER_ACTION">BROWSER_ACTION</option>
            </b-select>
          </b-field>
          <b-field label="Webview title*:">
            <b-input
              v-model="actions.title"
              type="text"
              class="form-control"
              expanded
            />
          </b-field>
          <b-field label="Webview top-title*:">
            <b-input
              v-model="actions.topTitle"
              type="text"
              class="form-control"
            />
          </b-field>
          <b-field label="Destination URL (openUrl):">
            <b-input
              v-model="actions.openUrl"
              type="text"
              class="form-control"
              placeholder="https://example.com"
            />
          </b-field>
          <b-field label="Close URL:">
            <b-input
              v-model="actions.closeUrl"
              type="text"
              class="form-control"
              placeholder="https://example.com"
            />
          </b-field>
          <!-- Adicionamos componente parámetros con nombre de parameters_prop y con listener para cambiar el estado de isParameters -->
          <C_Parameters
            :parameters_prop="parameters"
            @isParameters="onIsParametersChange"
          ></C_Parameters>
        </b-collapse>
        <hr />
        <div class="buttons">
          <b-button type="is-primary" @click="submit" expanded
            >Get JSON offer</b-button
          >
        </div>
      </div>
      <div class="column is-6" v-if="final_json">
        <div class="buttons">
          <b-button
            @click="copyText"
            icon-left="content-copy"
            class="is-secondary"
          >
            Copy to clipboard
          </b-button>
        </div>
        <pre id="canva">{{ final_json }}</pre>
      </div>
    </div>
  </div>
</template>
  
  <script>
import C_Parameters from "../components/C_Parameters.vue";
import GenericVariables from "../components/C_Generic_Variables.vue";

export default {
  components: {
    C_Parameters,
    GenericVariables,
  },
  data() {
    return {
      offer: {
        id: "",
        groupId: "",
        locationId: "",
        rendering: null,
        renderingType: {},
      },
      renderingImageBanner: {
        id: "",
        type: "",
        url: "",
        action: {},
      },
      renderingIconBanner: {
        id: "",
        type: "",
        icon: undefined,
        backgroundAlternateText: undefined,
        backgroundColor: undefined,
        title: "",
        titleTextColor: "",
        action: {},
      },
      actions: {
        type: "",
        operationalAction: {
          useCase: "useCase",
          type: "BACKGROUND_ACTION",
        },
        title: "",
        topTitle: "",
        openUrl: "",
        closeUrl: "",
        parameters: {},
      },
      parameters: [
        {
          key: "token",
          value: "$TOKEN",
        },
      ],
      defaultParameters: {
        token: "$TOKEN",
      },
      final_json: undefined,
      isParameters: false,
      isRenderOpen: true,
    };
  },
  methods: {
    submit() {
      let renderType = {}; //variable auxiliar para asociar el obejto renderType
      switch (this.offer.rendering) {
        case "IMAGE_BANNER":
          renderType = this.renderingImageBanner;
          break;
        case "ICON_BANNER":
          renderType = this.renderingIconBanner;
          break;
        case "BANNER":
        //this.banner = true;
      }
      //asignamos variable iguales al futuro objeto rederingType
      renderType.id = this.offer.id;
      renderType.type = this.offer.rendering;
      //asignamos parametros en caso de que sea necesario
      if (this.isParameters) {
        this.actions.parameters = this.setParameters();
      } else {
        this.actions.parameters = this.defaultParameters;
      }
      //asignamos el action al futuro objeto rederingType
      renderType.action = this.actions;
      //adicionamos el objeto renering type al objeto rto
      this.offer.renderingType = renderType;
      //limpiamos y formateamos JSON
      this.final_json = JSON.stringify([this.offer], null, 2);
      this.scroll("canva");
      this.snackbar("The JSON has been generated successfully!");
      this.$GA.sendGenerateOffer(this.offer, this.$route);
    },
    copyText() {
      navigator.clipboard.writeText(this.final_json);
      this.snackbar("JSON copied to clipboard!");
    },
    snackbar(text) {
      this.$buefy.snackbar.open(text);
    },
    async scroll(elementId) {
      await this.$nextTick();
      document.getElementById(elementId).scrollIntoView({
        behavior: "auto",
        block: "center",
        inline: "center",
      });
    },
    onIsParametersChange(value) {
      this.isParameters = value;
    },
    setParameters() {
      var newParameters = {};
      this.parameters.forEach((item) => {
        if ((item.key !== "" || undefined) && (item.value !== "" || undefined))
          newParameters[item.key] = item.value;
      });
      return newParameters;
    },
  },
};
</script>
  
  <style>
html,
body {
  height: 100%; /*both html and body*/
}
p.title.is-5.is-icon {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  margin-top: 1rem;
  align-items: center;
}
body {
}
</style>
  