export const GA = {
    sendPageView: (route) => {
        window.dataLayer = window.dataLayer || [];
        window.dataLayer.push({
            'event': 'page_view',
            'page_location': window.location.origin + route.fullPath,
            'page_name': route.name
        });
    },
    sendGenerateOffer: (offer, route) => {
        window.dataLayer = window.dataLayer || [];
        window.dataLayer.push({
            'event': 'generate_json',
            'page_location': window.location.origin + route.fullPath,
            'page_name': route.name,
            'event_type': 'offer',
            'offer_id': offer.id ? offer.id : "Not set",
            'offer_location_id': offer.locationId ? offer.locationId : "Not set",
            'offer_group_id': offer.groupId ? offer.groupId : "Not set",
            'offer_rendering': offer.rendering ? offer.rendering : "Not set",
            'offer_items': offer.renderingType.items ? offer.renderingType.items.length : 1
        });
    },
    sendGenerateAction: (action, route) => {
        window.dataLayer = window.dataLayer || [];
        window.dataLayer.push({
            'event': 'generate_json',
            'event_type': 'action',
            'page_location': window.location.origin + route.fullPath,
            'page_name': route.name,
            "action_type": action.renderActiontype ? action.renderActiontype : 'Not set',
            "action_title": action.title  ? action.title : 'Not set',
            "action_top_title": action.topTitle  ? action.topTitle : 'Not set',
            "action_open_url": action.openUrl  ? action.openUrl : 'Not set',
            "action_close_url": action.closeUrl  ? action.closeUrl : 'Not set',
            "action_parameters": action.parameters ? Object.keys(action.parameters).length : 0
        
        });
    }
}