<template>
  <section v-if="!isCookieAccepted">
    <div class="cookiesContent" id="cookiesPopup">
      <!-- <button class="close">✖</button> -->
      <img
        src="../assets/cookie.png"
        alt="cookies-img"
      />
      <p class="has-text-centered">
        We use cookies for improving user experience, analytics and marketing.
      </p>
      <b-button @click="cookieClickedAccept" class="accept">Agree!</b-button>
    </div>
</section>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      isCookieAccepted: false,
    };
  },
  methods: {
    cookieClickedAccept() {
      this.$cookies.set("cookie_consent", true);
      this.isCookieAccepted = true;
    },
    cookieRemovedCookie() {
      this.isCookieAccepted = false;
      this.$cookies.remove("cookie_consent", true);
      this.$refs.cookiePanel.init();
    },
    checkCookieStatus() {
      this.$cookies.get("cookie_consent") ? this.isCookieAccepted = true : this.isCookieAccepted = false;
    }
  },
  mounted () {
    this.checkCookieStatus();
  },
};
</script>


