<template>
  <div class="container px-1">
    <p class="title is-3">Carrousel banners</p>
    <div class="columns">
      <div class="column is-6">
        <GenericVariables :isCarousel_prop="true" :offer_prop="offer"></GenericVariables>
        <div
          class="column is-11 is-offset-1"
          v-for="(item, index) in itemsCarrousel"
          :key="index"
        >
          <p class="title is-5 is-icon">
            Offer {{ index + 1 }} configuration
            <b-button
              v-if="item.type !== null || item.id !== '' || index !== 0"
              size="is-small"
              rounded
              inverted
              type="is-danger"
              @click="deleteItem(item, index)"
              icon-right="delete"
              >
              Remove offer
              </b-button>
          </p>
          <b-field label="Offer ID*:">
            <b-input
              v-model="item.id"
              type="text"
              class="form-control"
              :placeholder="'e.g. carousel offer ' + (index + 1)"
              expanded
            >
            </b-input>
          </b-field>
          <b-field label="Render type*:">
            <b-select
              placeholder="Select a render type"
              :value="item.type"
              @input="item.type = $event.toString()"
              expanded
            >
              <option value="IMAGE_BANNER">IMAGE_BANNER</option>
              <option value="ICON_BANNER">ICON_BANNER</option>
            </b-select>
          </b-field>
          <!-- IMAGE BANNER -->
          <div
            class="column is-11 is-offset-1"
            v-if="item.type == 'IMAGE_BANNER'"
          >
            <p class="title is-5">Image banner variables</p>
            <b-field label="Picture/Image URL*:">
              <b-input
                v-model="item.url"
                type="text"
                class="form-control"
                placeholder="https://example.com"
              />
            </b-field>
          </div>

          <!-- ICON BANNER -->
          <div
            class="column is-11 is-offset-1"
            v-if="item.type == 'ICON_BANNER'"
          >
            <p class="title is-5">Icon banner variables</p>
            <b-field label="Icon URL*:">
              <b-input
                v-model="item.icon"
                type="text"
                class="form-control"
                placeholder="https://example.com"
              />
            </b-field>
            <b-field label="Background color (hex format):">
              <b-input
                v-model="item.backgroundColor"
                type="text"
                class="form-control"
                placeholder="#000000"
                expanded
              />
              <b-colorpicker
                :value="item.backgroundColor"
                @input="item.backgroundColor = $event.toString('hex')"
              />
            </b-field>
            <b-field label="Banner text*:">
              <b-input
                v-model="item.title"
                type="text"
                class="form-control"
                placeholder="Texto de pruebas"
              />
            </b-field>
            <b-field label="Text color (hex format)*:">
              <b-input
                v-model="item.titleTextColor"
                type="text"
                class="form-control"
                placeholder="#FFFFFF"
                expanded
              />
              <b-colorpicker
                :value="item.titleTextColor"
                @input="item.titleTextColor = $event.toString('hex')"
              />
            </b-field>
          </div>

          <!-- ACTION -->
          <div
            class="column is-11 is-offset-1"
            v-if="item.type == 'IMAGE_BANNER' || item.type == 'ICON_BANNER'"
          >
            <p class="title is-5">Action configuration</p>
            <b-field label="Action type*:">
              <b-select
                v-model="item.action.type"
                class="form-control"
                expanded
              >
                <option value="WEBVIEW_ACTION" selected>WEBVIEW_ACTION</option>
                <option value="FEATURE_ACTION">FEATURE_ACTION</option>
                <option value="BROWSER_ACTION">BROWSER_ACTION</option>
              </b-select>
            </b-field>
            <b-field label="Webvie titlew">
              <b-input
                v-model="item.action.title"
                type="text"
                class="form-control"
                expanded
              />
            </b-field>
            <b-field label="Webvie top-title:">
              <b-input
                v-model="item.action.topTitle"
                type="text"
                class="form-control"
              />
            </b-field>
            <b-field label="Destination URL (openUrl) *:">
              <b-input
                v-model="item.action.openUrl"
                type="text"
                class="form-control"
                placeholder="https://example.com"
              />
            </b-field>
            <b-field label="Close URL:">
              <b-input
                v-model="item.action.closeUrl"
                type="text"
                class="form-control"
                placeholder="https://example.com"
              />
            </b-field>
            <!-- Adicionamos componente parámetros con nombre de parameters_prop y con listener para cambiar el estado de isParameters -->
            <CompParameters
              :parameters_prop="parameters[index]"
              :isParameters_prop="isParameters[index]"
              @isParameters="onIsParametersChange($event, index)"
            ></CompParameters>
          </div>
          <div class="buttons is-centered">
            <b-button
              type="is-success is-light"
              size="is-small"
              @click="addItem(index)"
              outlined
              icon-left="plus"
            >
              Add new offer below</b-button
            >
            <b-button
              type="is-warning is-light"
              size="is-small"
              @click="
                duplicateItem(
                  item,
                  isParameters[index],
                  parameters[index],
                  index
                )
              "
              outlined
              icon-left="content-copy"
            >
              Duplicate offer below</b-button
            >
          </div>
        </div>
        <hr />
        <div class="buttons">
          <b-button type="is-primary" @click="submit" expanded
            >Get JSON offer</b-button
          >
        </div>
      </div>
      <div class="column is-6" id="canva" v-if="final_json">
        <div class="buttons">
          <b-button
            @click="copyText"
            icon-left="content-copy"
            class="is-secondary"
          >
            Copy to clipboard
          </b-button>
        </div>
        <pre id="canva_carrousel">{{ final_json }}</pre>
      </div>
    </div>
  </div>
</template>
  
  <script>
import CompParameters from "../components/C_Parameters.vue";
import GenericVariables from "../components/C_Generic_Variables.vue";

export default {
  name: "App",
  components: {
    CompParameters,
    GenericVariables
  },
  data: () => ({
    offer: {
        id: "",
        groupId: "",
        locationId: "",
        rendering: "CARROUSEL",
        renderingType: {},
      },
    itemsCarrousel: [
      {
        id: "",
        type: null,
        backgroundColor: undefined,
        titleTextColor: undefined,
        action: {
          type: null,
          operationalAction: {
            useCase: "useCase",
            type: "BACKGROUND_ACTION",
          },
          parameters: {
            token: "$TOKEN",
          },
        },
      },
    ],
    isParameters: [false],
    parameters: [
      [
        {
          key: "token",
          value: "$TOKEN",
        },
      ],
    ],
    defaultParameters: {
      token: "$TOKEN",
    },
    final_json: undefined,
  }),
  methods: {
    addItem(index) {
      var newItem = {
        id: "",
        type: null,
        backgroundColor: undefined,
        titleTextColor: undefined,
        action: {
          type: "",
          operationalAction: {
            useCase: "useCase",
            type: "BACKGROUND_ACTION",
          },
          parameters: {
            token: "$TOKEN",
          },
        },
      };
      var newIsParameters = false;
      var newParameters = [
        {
          key: "token",
          value: "$TOKEN",
        },
      ];
      this.itemsCarrousel.splice(index + 1, 0, newItem);
      this.isParameters.splice(index + 1, 0, newIsParameters);
      this.parameters.splice(index + 1, 0, newParameters);
    },
    deleteItem(item, index) {
      this.itemsCarrousel.splice(index, 1);
      this.isParameters.splice(index, 1);
      this.parameters.splice(index, 1);
      if (this.itemsCarrousel.length === 0) {
        //si nos quedamos sin ofertas añadimos una en blanco
        this.addItem();
      }
      this.snackbar(
        "The offer " + (index + 1) + " has been removed successfully!"
      );
    },
    duplicateItem(item, isParameter, parameter, index) {
      this.itemsCarrousel.splice(index + 1, 0, JSON.parse(JSON.stringify(item)));
      this.isParameters.splice(index + 1, 0, isParameter);
      this.parameters.splice(index + 1, 0, JSON.parse(JSON.stringify(parameter)));
    },
    snackbar(text) {
      this.$buefy.snackbar.open(text);
    },
    submit() {
      this.setParameters();
      this.offer.renderingType.items = this.itemsCarrousel;
      this.final_json = JSON.stringify([this.offer], null, 2);
      this.scroll("canva_carrousel");
      this.snackbar("The JSON has been generated successfully!");
      this.$GA.sendGenerateOffer(this.offer, this.$route)
    },
    copyText() {
      navigator.clipboard.writeText(this.final_json);
      this.snackbar("JSON copied to clipboard!");
    },
    async scroll(elementId) {
      await this.$nextTick();
      document.getElementById(elementId).scrollIntoView({
        behavior: "auto",
        block: "center",
        inline: "center",
      });
    },
    onIsParametersChange(value, index) {
      this.isParameters[index] = value;
    },
    setParameters() {
      for (var i = 0; i < this.itemsCarrousel.length; i++) {
        if (this.isParameters[i]) {
          var newParameters = {};
          this.parameters[i].forEach((item) => {
            if (
              (item.key !== "" || undefined) &&
              (item.value !== "" || undefined)
            )
              newParameters[item.key] = item.value;
          });
          this.itemsCarrousel[i].action.parameters = newParameters;
        } else {
          this.itemsCarrousel[i].action.parameters = this.defaultParameters;
        }
      }
    }
  }
}
</script>
  
  <style>
html,
body {
  height: 100%; /*both html and body*/
}
body {
}
</style>
  