<template>
  <section>
    <p class="title is-4">Generic variables</p>
    <b-field label="Offer ID*:">
      <b-input
        v-model="offer.id"
        type="text"
        class="form-control"
        placeholder="e.g. NBO"
        expanded
      >
      </b-input>
    </b-field>
    <b-field label="Location ID*:">
      <b-autocomplete
        v-model="offer.locationId"
        :data="filterLocationID"
        group-options="items"
        group-field="type"
        placeholder="e.g. MOVGPC0001"
        :open-on-focus="true"
      >
      </b-autocomplete>
    </b-field>
    <b-field label="Group ID*:">
      <b-autocomplete
        v-model="offer.groupId"
        :data="filterGroupID"
        group-options="item"
        group-field="type"
        placeholder="e.g. MOVGPC0001"
        :open-on-focus="true"
      >
      </b-autocomplete>
    </b-field>
    <b-field label="Render type*:" v-if="!isCarousel">
      <b-select
        expanded
        placeholder="Select a render type"
        :value="offer.rendering"
        @input="offer.rendering = $event.toString()"
      >
        <option value="IMAGE_BANNER">IMAGE_BANNER</option>
        <option value="ICON_BANNER">ICON_BANNER</option>
        <!-- <option value="banner">BANNER</option>-->
      </b-select>
    </b-field>
  </section>
</template>
  
  <script>
const locations = require("../locations.json");

export default {
  name: "App",
  props: {
    isCarousel_prop: {
      type: Boolean,
      default: false
    },
    offer_prop: Object
  },
  data: function () {
    return {
      isCarousel: this.isCarousel_prop,
      offer: this.offer_prop,
      locations
    };
  },
  computed: {
    filterLocationID() {
      const newData = [];
      this.locations.forEach((element) => {
        const items = element.locationsID.filter(
          (item) =>
            item.toLowerCase().indexOf(this.offer.locationId.toLowerCase()) >= 0
        );
        if (items.length) {
          newData.push({ type: element.type, items });
        }
      });
      return newData;
    },
    filterGroupID() {
      const newData = [];
      this.locations.forEach((element) => {
        var item = element.groupID;
        if(item.toLowerCase().includes(this.offer.groupId.toLowerCase())){
          item = [item]
          newData.push({ type: element.type, item });
        }  
      });
      return newData;
    },
  },
};
</script>
  