import Vue from 'vue'
import VueRouter from 'vue-router'
import ImageIconBanner from '../views/Image_icon_banner.vue'
import Carrousel from '../views/Carrousel.vue'
import Actions from '../views/Actions.vue'
import 'buefy/dist/buefy.css'
import {GA} from '../GA_functions'

Vue.prototype.$GA = GA

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/image-icon-banner-generator'
  },
  {
    path: '/image-icon-banner-generator',
    name: '/image_icon_banner_generator',
    component: ImageIconBanner,
    meta: {
      title: 'Image & icon banner generator'
    }
  },
  {
    path: '/carrousel-generator',
    name: '/carrousel_generator',
    component: Carrousel,
    meta: {
      title: 'Carousel generator'
    }
  },
  {
    path: '/actions-generator',
    name: '/action_generator',
    component: Actions,
    meta: {
      title: 'Action generator'
    }
  }
]

const router = new VueRouter({
  routes,
})

router.beforeEach((to, from, next) => {
  if(to !== from){
    window.document.title = to.meta && to.meta.title ? to.meta.title : 'Page not found';
    GA.sendPageView(to);
  }
  next();
})

export default router
